.dynamic-delete-button {
  cursor: pointer;
  position: relative;
  top: 4px;
  font-size: 24px;
  color: #999;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.dynamic-delete-button:hover {
  color: #777;
}
.dynamic-delete-button[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}
.pdynamic {
  padding: 1rem 3rem .5rem 3rem;
}
.pdynamic .border {
  padding-top: 1rem;
  border-color: #d9d9d9;
  border-style: dashed;
  border-width: 1px;
}
